@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&family=Rubik:wght@400;700&display=swap');

.header{
  font: 400 1rem Roboto, sans-serif;
  background-color: #0A4929;
  display: flex;
  align-items: center;
  height: 3rem;
  position: fixed;
  top: 0;
  width: 100vw;
  box-shadow: 0px 0.1px 5px;
}

.header img {
  width: 125px;
  height: 45px;
  margin-left: 0;
}

.menu-area ul{
  height: 3rem;
  margin-left: 10px;
}

.menu-area li a{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-decoration: none;
  background-color: #0A4929;
  color: #f6fffa;
  transition: 0.4s;
}

.menu-area a:hover .menu-area ul{
  background-color: #508059;
  color: #0A4929;
}

.menu-area ul li a{
  width: 150px;
}

.menu-area ul li .dropdown{
  position: absolute;
  margin: 0px;
  box-shadow: 0px 1px 5px #b6b6b6;
  display: none;
  width: 300px;
  height: auto;
}

.menu-area ul li .dropdown li{
  position: relative;
  width: 100%;
  border-top: 1px solid #e2e2e2;
}

.menu-area ul li .dropdown a{
  background-color: #fff;
  color: #0A4929;
  transition: 0.3s;
  width: 100%;
  height: 100%;
  padding: 4px;
}

.menu-area ul li:hover .dropdown {
  display: flex;
}

.menu-area ul li:hover a{
  background-color: #508059;
  color: #fff;
}

.menu-area ul li .dropdown a:hover{
  background-color: #0A4929;
  color: #fff;
}

.menu-area ul{
  list-style: none;
  display: flex;
}

.menu-area ul li{
  position: relative;
  width: 100%;
  text-align: center;
}

.dropdown{
  display: flex;
  flex-direction: column;
  width: 100%;
}

.dropdown li{
  width: 200px;
}
/* USUÁRIO */
.user{
  padding-top: 0px;
  color: white;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.user div{
  display: flex;
  flex-direction: column;
  margin-right: 0px;
}

.user div p{
  display: flex;
  flex-direction: row-reverse;
  margin-right: 30px;
}

#selected_company{
  font-size: 14px;
}

.user div a{
  text-decoration: none;
  color: white;
  transition: 0.2s;
}

.user div a:hover{
  text-decoration: none;
  color: #d4d4d4;
}

.user div a .exit{
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  margin-right: 30px;
  margin-top: 2px;
}

.user div .exit #exit-icon{
  margin-left: 4px;
}
