  .container-services-l .lds-dual-ring{
    margin-top: 3rem;
    display: flex;
    position: absolute;
    width: 100vw;
    height: 90vh;
    background-color: white;
    opacity: 0.8;
  }
  
  
  .lds-dual-ring {
     
      align-items: center;
      justify-content: center;
      color: #0A4929;
     
   
  }
  .lds-dual-ring:after {
      content: " ";
      display: block;
      width: 64px;
      height: 64px;
      margin: 8px;
      border-radius: 50%;
      border: 8px solid #508059;
      border-color: #508059 transparent #508059 transparent;
      -webkit-animation: lds-dual-ring 1.2s linear infinite;
              animation: lds-dual-ring 1.2s linear infinite;
  }
  
  @-webkit-keyframes lds-dual-ring {
      0% {
          transform: rotate(0deg);
      }
      100% {
          transform: rotate(360deg);
      }
  }
  
  @keyframes lds-dual-ring {
      0% {
          transform: rotate(0deg);
      }
      100% {
          transform: rotate(360deg);
      }
  }
  
