@media(max-width:1400px){

    .orderReq-container .selection {
        display: flex;
        flex-direction: row;
    }

    .orderReq-container .selection img {
        margin-left: 10rem;
        width: 8rem;
    }
}

.orderReq-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-items: center;
    background-color: #EDEDED;
    width: 100vw;
    height: 100vh;
    color:#073c14;
    font-size: 1.4rem;
}

.orderReq-container .txtReq-box{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.orderReq-container .selectionReq{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 100%;
}