.viewOrder-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: #EDEDED;
    height: 100vh;
    width: 100vw;
}

.viewOrder-container h1 {
    color: #073c14;
    font-size: 1.4rem;
}

.viewOrder-container form {
    display: grid;
    grid-template-columns: 2fr 2fr;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.viewOrder-container footer {
    height: 45px;
    position: fixed;
    bottom: 0;
    width: 200%;
    transition: 0.4s;
    display: grid;
    grid-template-columns: 2fr 2fr;
}

.viewOrder-container footer p{
    margin-left: 1.5rem;
    font-size: 1rem;
    align-self: center;
    color: white
}
.viewOrder-container footer span{
    margin-right: 7rem;
    font-size: 1rem;
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    color: white
}

.viewOrder-container .search-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.viewOrder-container .search-box .input{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.viewOrder-container .search-box input {
    border: none;
    border-bottom: 1px solid #0D561D;
    width: 15rem;
    margin-top: 1.5rem;
    margin-bottom: 2.5rem;
    font-size: 1.2rem;
    margin-right:2.5rem ;
    background-color: #EDEDED;
    outline: none;
    border-radius: 5px;
}