.selOrder-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: #EDEDED;
    height: 100vh;
    width: 100vw;

}

.selOrder-container form {
    display: grid;
    grid-template-columns: 2fr 2fr;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.selOrder-container footer{

    height: 45px;
    position: fixed;
    bottom: 0;
    width: 200%;
    transition: 0.4s;
    display: grid;
    grid-template-columns: 2fr 2fr;
}


.selOrder-container footer p{
    margin-left: 1.5rem;
    font-size: 1rem;
    align-self: center;
    color: white
}
.selOrder-container footer span{
    margin-right: 7rem;
    font-size: 1rem;
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    color: white
}

.selOrder-container .push{
    font-size: 1.8rem;
    height: 50px;
}

.selOrder-container .selection {
    font-size: 1.1rem;
    display: flex;
    flex-direction: column;
}


.selOrder-container .selection p {
    font-size: 1.1rem;
}


.selOrder-container .selection h3 {

    font-size:  1rem;
    color: red;
    font-family: roboto;
    font-weight: 100;
}




.selOrder-container .txt-box{
    background-color: #EDEDED; 
    display: flex;  
    align-items: center;
    justify-content: center;
    justify-items: center;
    justify-self: center;
    align-content: center;
    flex-direction: column;
}

.selOrder-container .txt-box p{
    background-color: #EDEDED;  
    color: #073c14;
    font-size: 1.4rem;
    width: 70%;
    display: flex;
    
}

.selOrder-container .txt-box h1{
    color: #073c14;
    font-size: 1.4rem;
    width: 70%;
    display: flex;
}

.selOrder-container .selection{
    display: flex;
    align-items: center;
    justify-content: center;
    justify-items: center;
    justify-self: center;
    align-content: center;
    color: #073c14
}

.selOrder-container .selection input{
    height: 2.8rem;
    width: 25rem;
    border:none;
    border-bottom: solid 1px black;
    background-color: #EDEDED ;
    font-size: 1.4rem;
    outline: none;
    margin-top: 0;
    
}

.selOrder-container .selection button{
    height: 2.8rem;
    border-radius: 40px;
    width: 10rem;
}


.selOrder-container .nf-box form p{
    margin-bottom: 1rem;
    font-size:1rem;
    background-color: #EDEDED !important;
}

.selOrder-container .nf-box button{
    background-color:#073c14;
    color: white;
    font-size: 1.1rem;
    margin-top: 8px;
    width: 8rem;
    border-radius: 40px;
    height: 2.3rem;
    transition: 0.3s;
    cursor: pointer;
    color: #EDEDED
}

.selOrder-container .nf-box button:hover{
    background-color: white;
    color: #073c14;

}

.selOrder-container .nf-box input{
    margin-top: 0.8rem;
    margin-bottom: 0.8rem;
    background-color: #073c14;
}

.selOrder-container .selection {
    
    margin-right: 20rem;
    
}

.selOrder-container .txt-box .loadOrder {
    margin-left: 20rem;
}

.nf-data {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #073c14;
}

.nf-data .nf-data-box{
    display: flex;
    flex-direction: column;
    background-color: #073c14;
    width: 500px;
    height: 300px;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 30px #b6b6b6;
    border-radius: 5px;
    margin-top: 6rem;
}

.nf-data .nf-data-box button{
    background-color:#073c14;
    color: white;
    font-size: 1.1rem;
    margin-top: 8px;
    width: 8rem;
    border-radius: 5px;
    height: 2.3rem;
    transition: 0.3s;
    cursor: pointer;
}

.nf-data .nf-data-box button:hover{
    background-color: white;
    color: #EDEDED;

}

.nf-data .nf-data-box input{
    margin-top: 0.8rem;
    margin-bottom: 0.8rem;
}



.nf-more-data{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5rem;
}

.nf-more-data .question{
    display: flex;
    flex-direction: row;
    margin-top: 4rem;
}

.nf-more-data .question button{
    background-color:#EDEDED;
    color: white;
    font-size: 1.1rem;
    margin-top: 8px;
    width: 8rem;
    border-radius: 5px;
    height: 2.3rem;
    transition: 0.3s;
    cursor: pointer;   
}

.nf-more-data .question button:hover{
background-color: white;
    color: #EDEDED;

}

@media(max-width:1400px){

    .selOrder-container .push{
        font-size: 1.2rem;
        height: 40px;
    }
    
    .selOrder-container .selection {
        font-size: 0.8rem;
        display: flex;
        flex-direction: column;
    }
    
    
    .selOrder-container .selection p {
        font-size: 0.8rem;
    }
    
    
    .selOrder-container .selection h3 {
    
        font-size:  0.8rem;
        color: red;
        font-family: roboto;
        font-weight: 100;
    }
  
    .selOrder-container .txt-box p{
        background-color: #EDEDED;  
        color: #073c14;
        font-size: 1rem;
        width: 70%;
        display: flex;
        
    }
    
    .selOrder-container .txt-box h1{
        color: #073c14;
        font-size: 1rem;
        width: 70%;
        display: flex;
    }
    .selOrder-container .selection input{
        height: 2.1rem;
        width: 20rem;
        border:none;
        border-bottom: solid 0.1rem black;
        background-color: #EDEDED ;
        font-size: 1rem;
        outline: none;
        margin-top: 0;
        
    }    

}

