#root{
    width: 90vw;
    height: 90vh;
}

body{
    background-color:  #EDEDED;
}

.viewNF-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100vw;
    height: 90vh;
    justify-content: center;
    align-items: center;
    background-color:  #EDEDED;
}

.viewNF-container .searchNF {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.viewNF-container .table-nf{

    display: grid;
    grid-template-columns: 1fr 1fr;
}

.viewNF-container .table-nf h1 {
    color:#0A4929
}

.viewNF-container .table-nf thead td:nth-child(1) {
    width: 15rem;
 
}

.viewNF-container .table-nf tr .empty{
    width: 4rem;

}


.viewNF-container .searchNF input {
    border: none;
    border-bottom: 1px solid #0D561D;
    width: 15rem;
    margin-bottom: 2.5rem;
    font-size: 1.2rem;
    background-color: #EDEDED;
    outline: none;
    border-radius: 5px;
}

.viewNF-container .table-nf table{
    border-spacing: 0;
    margin-right: 10px;
    width: 800px;
    margin-bottom: 10px;
    border-collapse:separate;
    border:solid rgba(142, 151, 144, 0.781) 1px;
    border-radius:6px;
    -moz-border-radius:6px;
}

.viewNF-container .table-nf thead td {
    background-color: #0A4929;
    color: white;
    text-align: center;
    border-top: none;
    
    
}
.viewNF-container .table-nf td{
    border: none;
    font-size: 1.2rem;
    padding: 3px;
    width: 10rem;
}

.viewNF-container .table-nf td:nth-child(2){
    width: 40rem;
}


.viewNF-container footer {
    height: 45px;
    position: fixed;
    bottom: 0;
    width: 200%;
    background-color: #0A4929;
    transition: 0.4s;
    display: grid;
    grid-template-columns: 2fr 2fr;
}
.viewNF-container footer:hover {
    background-color: #508059;
    cursor: pointer;
}


.viewNF-container footer  p {
    margin-left: 1.5rem;
    font-size: 1rem;
    align-self: center;
    color: white
}

.container-services-lnone{
    display: none;
  
  }

  
  .container-services-l .lds-dual-ring{
    margin-top: 3rem;
    display: flex;
    position: absolute;
    width: 100vw;
    height: 90vh;
    background-color: white;
    opacity: 0.8;
  }
  
  
  .lds-dual-ring {
     
      align-items: center;
      justify-content: center;
      color: #0A4929;
     
   
  }
  .lds-dual-ring:after {
      content: " ";
      display: block;
      width: 64px;
      height: 64px;
      margin: 8px;
      border-radius: 50%;
      border: 8px solid #508059;
      border-color: #508059 transparent #508059 transparent;
      animation: lds-dual-ring 1.2s linear infinite;
  }
  
  @keyframes lds-dual-ring {
      0% {
          transform: rotate(0deg);
      }
      100% {
          transform: rotate(360deg);
      }
  }
  