.first{
  display: flex;
  flex-direction: column;
  background-color: #EDEDED;
  height: 100vh;
  width: 100vw;
  
  /* width: 99vw; */
}

.first form .home-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;

}

.first footer{

  height: 45px;
  position: fixed;
  bottom: 0;
  width: 100%;
  transition: 0.4s;
  display: grid;
  grid-template-columns: 2fr 2fr;

}

.first footer p{
  margin-left: 1.5rem;
  font-size: 1rem;
  align-self: center;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  color: white;

}

footer button{
  width: 100vw;
  background-color: #0A4929;
  display: flex;
  cursor: pointer;
  align-items: center;
  transition: 0.2s;
  border: 1px solid #0A4929;
}

footer button:hover{
  background-color: #508059;
}


.first footer .footer-right{
  margin-right: 3rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.first footer .footer-left {

  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.first footer span{
  margin-right: 7rem;
  font-size: 1rem;
  align-self: center;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  color: white
}

.first .home-container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.first .home-container h1{
  margin-top:  8rem;
  color: #0A4929
}

.first .home-container .home-box{
    display: flex;
    flex-direction: column;
    width: 500px;
    height: 300px;
    align-items: center;
    justify-content: center;
    
    border-radius: 5px;
    margin-top: 4rem;
}

.home-container .home-box form{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.home-container .home-box select{
    width: 15.5rem;
    height: 2.5rem;
    color: black;
    border-radius: 5px;
    border: none;
    outline: none;
    border-bottom: 1px solid black;
    background-color: #EDEDED; 
    font-size: 1.2rem;
    outline: none;
}

.home-container .home-box button{
  /* background-color:#0A4929; */
  color: white;
  font-size: 1.3rem;
  margin-top: 10px;
  border: none;
  border-bottom: 1px solid black;
  width: 15rem;
  border-radius: 40px;
  height: 2rem;
  transition: 0.3s;
  cursor: pointer;
  margin-left: 1rem;
}

.home-container .home-box button:hover{
  background-color: #508059;
  color: white;
}

@media(max-width:1400px){


  .home-container h1{
    font-size: 1.5rem;
  }
  .home-container .home-box form{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  .home-container .home-box select{
      width: 11.5rem;
      height: 2rem;
      color: black;
      border-radius: 5px;
      background-color: #EDEDED; 
      font-size: 0.8rem;
      outline: none;
  }
  
  .home-container .home-box button{
    background-color:#0A4929;
    color: white;
    font-size: 0.8rem;
    margin-top: 5px;
    width: 10.5rem;
    border-radius: 40px;
    height: 2.5rem;
    transition: 0.3s;
    cursor: pointer;
    margin-left: 1rem;
  }
  
  .home-container .home-box button:hover{
    background-color: #508059;
    color: white;
  }
  .first .home-container .home-box{
    margin-top: 0;
  }
}