.container {
    display: grid;
    grid-template-rows: 1fr 2fr;
    background-color: #EDEDED;
    height: 99.7vh;
}

.container .top {

   width: 100%;
   height: 100%;
   background-color:  #EDEDED;
   padding-left: 6rem;
}

.container .top h1{
    margin-top: 8rem;
    color: #0D561D;
}

.container .top input {
    text-align: center;
    margin-top: 1.7rem;
    border: none;
    border-bottom: 1px solid #0D561D;
    width: 8rem;
    margin-bottom: 2.5rem;
    font-size: 1.2rem;
    background-color: #EDEDED;
    outline: none;
    border-radius: 5px;
}

.container .top button {
    background-color: #0D561D;
    color: white;
    border-radius: 5px;
    margin-left: 0.8rem;
    border: none;
    height: 1.6rem;
    width: 5rem;
    transition: 0.2s;
    outline: none;
}

.container .top button:hover {
    cursor: pointer;
    background-color:#508059;

}

.container .bottom {
   padding-left: 6rem;
    width: 100%;
   height: 100%;
   background-color:  #EDEDED;
    
}

.container footer {
    height: 45px;
    position: fixed;
    bottom: 0;
    width: 200%;
    background-color: #0A4929;
    transition: 0.4s;
    display: grid;
    grid-template-columns: 2fr 2fr;
}
.container footer:hover {
    background-color: #508059;
    cursor: pointer;
}


.container footer  p {
    margin-left: 1.5rem;
    font-size: 1rem;
    align-self: center;
    color: white
}

.container p{
    margin-bottom: 1rem;
    color:#0A4929;
    font-weight: 500;
    font-size: 1.2rem;
}
/* tabela formacao de lote lado esquerdo */

.container .left-box table {
 text-align: center;
 background-color: #e2e2e2;
 border-spacing: 0;
 
}
.container .left-box table thead {
    background-color: #0D561D;
    color: white
}


.container .left-box table td {
    border: none;
    border-bottom: 1px solid black;
    height: 2.3rem;
    width: 8rem;
}

.container .left-box table tr:nth-child(even){
    background-color: #d8d8d8;
}

.container .right-box {
  
    margin-left: 6rem;
    margin-bottom: 6rem;
    display: flex;
    flex-direction: column;
    color:  #0A4929;
    justify-content: center;
}


/* tabela usina */

.container .right-box table {
    text-align: center;
    background-color: #e2e2e2;
    border-spacing: 0;
    
   }
   .container .right-box table thead {
       background-color: #0D561D;
       color: white
   }
   
   
   .container .right-box table td {
       border: none;
       border-bottom: 1px solid black;
       height: 2.3rem;
       width: 8rem;
   }