.main-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 99.9vw;
    height: 99.9vh;
    background-color: #EDEDED;
}

.main-container p {

    color: #073c14;
    font-size: 1.4rem;
    display: flex;
}


.main-container footer {
    height: 45px;
    position: fixed;
    bottom: 0;
    width: 200%;
    background-color: #0A4929;
    transition: 0.4s;
    display: grid;
    grid-template-columns: 2fr 2fr;
}
.main-container footer:hover {
    background-color: #508059;
    cursor: pointer;
}


.main-container footer  p {
    margin-left: 1.5rem;
    font-size: 1rem;
    align-self: center;
    color: white
}

.main-container .left-box{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main-container .left-box h1 {
    color: #0A4929 ;
}

.main-container .right-box{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main-container .right-box input{
    border: none;
    border-bottom: 1px solid #0D561D;
    width: 15rem;
    margin-bottom: 2.5rem;
    font-size: 1.2rem;
    background-color: #EDEDED;
    outline: none;
    border-radius: 5px;
}

