.orderChk-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: #EDEDED;
    height: 100vh;
    width: 100vw;
    color:#073c14;
    font-size: 1.4rem;
    border: 1px solid black;
}

.txtChkQ-box {
    border: 1px solid black;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.nfChkQ-data-box {

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.nfChkQ-data-box button{
    width: 8rem;
    height: 2rem;
    border-radius:15px;
    margin-left: 1rem;
    transition: 0.2s;
}
.nfChkQ-data-box button:hover{
    background-color: #508059;
}

.orderChk-container form {
    display: grid;
    grid-template-columns: 2fr 2fr;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.orderChk-container .txtChk-box{
    display: flex;
    align-items: center;
    justify-content: center;
}

.orderChk-container .nfChk-data-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.orderChk-container .nfChk-data-box input{
    border: none;
    border-bottom: 1px solid #0D561D;
    width: 15rem;
    margin-bottom: 2.5rem;
    font-size: 1.2rem;
    background-color: #EDEDED;
    outline: none;
    border-radius: 5px;
}

button{
background-color:#0A4929;
color: white;
/* font-size: 0.8rem; */
transition: 0.3s;
cursor: pointer;
}

.ordeChk-container .nfChk-data-box h1{
    font-size: 1.3rem;
    color: #0A4929;
    
    }

    .orderChk-container footer{
        color: white;
        height: 45px;
        position: fixed;
        bottom: 0;
        width: 200%;

        transition: 0.4s;
        display: grid;
        grid-template-columns: 2fr 2fr;
    }
    
    .orderChk-container footer p{
        margin-left: 1.5rem;
        font-size: 1rem;
        align-self: center;
        color: white
    }
    

@media(max-width:1400px){

    .orderChk-container .txtChk-box h1{
        font-size: 1.3rem;
    }
    .orderChk-container .nfChk-data-box {
        display: flex;
        flex-direction: column;
    }
    .orderChk-container .nfChk-data-box input{
        font-size: 1rem; 
        margin-bottom: 0.5rem;
        width: 25rem;
    }

    .orderChk-container .nfChk-data-box button{
    background-color:#0A4929;
    color: white;
    font-size: 0.8rem;
    margin-top: 5px;
    width: 10.5rem;
    border-radius: 40px;
    height: 2.5rem;
    transition: 0.3s;
    cursor: pointer;
    margin-left: 1rem;
    }

    .ordeChkr-container .nfChk-data-box h1{
        font-size: 1.3rem;
        color: #0A4929;
        
        }

        .ordeChkr-container footer{
            color: white;
            height: 45px;
            position: fixed;
            bottom: 0;
            width: 200%;
            background-color: #0A4929;
            transition: 0.4s;
            display: grid;
            grid-template-columns: 2fr 2fr;
        }
        
        .ordeChkr-container footer:hover{
           
            background-color: #508059;
            cursor: pointer;
        }

        .ordeChkr-container footer p{
            margin-left: 1.5rem;
            font-size: 1rem;
            align-self: center;
            color: white
        }
        

}


