body {
  height: 100vh;
  width: 100vw;
  margin: 0px;
  padding: 0px;
}


.container {

  display: grid;
  grid-template-columns: 50% 50%;
  background-color: #0A4929 !important;

}

.container .left-box{

  font-size: 0.8rem;
  margin-left: 2rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  color: #74BAA0
}

.container .left-box .first-h1{
  color: white;
  margin-bottom: 4rem;
  margin-top: 0;
}

.container .left-box h1{
  margin-bottom: 2rem;
    font-size: 2.5rem;;
}

.container .left-box h2{

  font-size: 1.8rem;
}

.container .left-box h3{
  color: white;
  margin-top: 3rem;
  font-size: 1.8rem;
}

.container .login-box {
  font-size: 1.1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50vw;
  height: 100vh;
  background-color: #EDEDED;
}

.container .login-box .action-form{
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  
  align-items: center;
  justify-content: center;

  
}



.container .login-box p{
  font-size: 1.1rem;

}

.container .login-box h3 {
  color: red;
  font-size: 0.8rem;
}

.container .login-box img {
  
  margin-bottom: 3rem;
  border-radius: 10px;
}

.container .login-box input {
  width: 15rem;
  margin-bottom: 1.4rem;
  height: 2rem;
}

.container .login-box button{
  background-color:#0A4929;
  color: white;
  font-size: 1rem;
  margin-top: 8px;
  width: 13rem;
  border-radius: 40px;
  height: 3rem;
  transition: 0.3s;
  cursor: pointer;
}

.container .login-box button:hover{
  background-color: white;
  color: #106B26;
 
}


@media(max-width:1400px){

  body {
    height: 100vh;
  }
  
  
  .container{
    display: grid;
    grid-template-columns: 50% 50%;
    background-color: #0A4929
  }
  
  .container .left-box{
    font-size: 1rem;
    display: flex;
    justify-content: center;
    margin-left: 2rem;
    flex-direction: column;
    color: #74BAA0
  }
  
  .container .left-box .first-h1{
    color: white;
    margin-bottom: 4rem;
    margin-top: 0;
    font-size: 1.8rem;
  }

  .container .left-box .first-h1{
    margin-bottom: 2rem;
    font-size: 1.7rem;
  }
  
  .container .left-box h1{
    margin-bottom: 2rem;
    font-size: 1.7rem;
  }

  .container .left-box h2{
   font-size: 1.2rem;
  }
  
  .container .left-box h3{
    color: white;
    margin-top: 3rem;
  }


  
  
  .container .login-box .action-form{
    display: flex;
    flex-direction: column;
    height: 100vh;
    border-radius: 5px;
    background-color: #F2F5F6;
    align-items: center;
    justify-content: center;
    
  }
  
  
  .container .login-box p{
    font-size: 1.4rem;
    display: flex;
    align-items: center;
  
  }
  
  .container .login-box h3 {
    color: red;
    font-size: 0.8rem;
    font-weight: 100;
  }
  
  .container .login-box img {
    width: 20%;
    margin-bottom: 3rem;
    border-radius: 10px;
  }
  
  .container .login-box input {
    width: 13rem;
    margin-bottom: 1.4rem;
    height: 2rem;
  }
  
  .container .login-box button{
    background-color:#0A4929;
    color: white;
    font-size: 0.8rem;
    margin-top: 8px;
    width: 10.5rem;
    border-radius: 40px;
    height: 2.5rem;
    transition: 0.3s;
    cursor: pointer;
  }
  
  .container .login-box button:hover{
    background-color: white;
    color: #106B26;
  }
}