.orderPkg-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background-color: #EDEDED;
    width: 100vw;
    height: 100vh;
    color:#073c14;
    font-size: 1.4rem;

}

.orderPkg-container .package-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.orderPkg-container .selectionPkg {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}


@media(max-width:1400px){

    .orderPkg-container .selectionPkg {
        display: flex;
        flex-direction: row;
    }

    .orderPkg-container .selectionPkg img {
        margin-left: 10rem;
        width: 8rem;
    }
}